:root {
  --timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  --animation-duration: 250ms;
}

#search-form {
  display: block;
  margin: 0 auto;
  font: inherit;
  padding: 8px;
  margin-bottom: 16px;
  background-color: aquamarine;
  text-align: center;
}

.gallery {
  /* display: grid;
  max-width: 1140px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 240px;
  grid-gap: 12px; */
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  margin-left: auto;
  margin-right: auto;
}

.photo-card {
  width: calc((100% - 90px) / 4);
  /* position: relative; */
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.info {
  display: flex;
  justify-content: space-evenly;
  font-size: 14px;
}

.gallery-image:hover {
  transform: scale(1.03);
}

.gallery-image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery-link {
  display: block;
  text-decoration: none;
  /* height: auto; */
  height: 200px;
}

.gallery-link:hover {
  cursor: zoom-in;
}

.info-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

button.load-more {
  display: block;
  margin-left: auto;
  margin-right: auto;
}